<template>
  <div class="loginBox">
    <div class="loginMainBox">
      <div class="loginbrand">
        <div class="usernameform">
          <el-form label-width="80px" :model="loginform">
            <el-form-item label="账号:">
              <el-input
                placeholder="请输入账号"
                v-model="loginform.username"
                clearable
              >
              </el-input>
            </el-form-item>
            <el-form-item label="密码:">
              <el-input
                placeholder="请输入密码"
                v-model="loginform.password"
                clearable
                show-password
              >
              </el-input>
            </el-form-item>

            <el-form-item label="验证码:">
              <div class="keyimgbox">
                <!-- 绑定placeholder  -->
                <el-input
                  :placeholder="postString"
                  v-model="loginform.checkKey"
                  clearable
                  style="width: 50%"
                >
                </el-input>
                <!-- <span style="width:50%;" :v-html="checkImg">{{checkImg}}</span> -->
                <span class="keyimg" @click="checkKey()">
                  <img src="" id="keyImg" />
                </span>
              </div>
            </el-form-item>
          </el-form>
          <div class="remeberMe">
            <el-checkbox v-model="loginform.checked">记住我</el-checkbox>
          </div>
          <div class="login_register">
            <el-button
              type="warning"
              style="margin-right: 40px"
              @click="tohome()"
              >登录</el-button
            >
            <el-button type="warning">注册</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QS from "qs";
export default {
  data() {
    return {
      checkImg: "",
      loginform: {
        username: "",
        password: "",
        time: "请输入验证码",
        checkKey: "",

        checked: false,
      },
    };
  },
  created() {
    this.checkKey();
    setInterval(() => {
      this.checkKey();
    }, 60000);
  },
  computed: {
    postString() {
      return this.loginform.time;
    },
  },
  methods: {
    checkKey() {
      //  const params = new URLSearchParams
      //  params.append('key',value)
      this.$http({
        method: "get",
        url: "/user/getImgCode",
        //header:{已经在main.js中写好了}
        //token:已经在main.js中写好了{现在用不着，后面登陆后的访问需要}
        //params：params(这里不需要传数据，所以不用写)
      }).then((res) => {
        console.log(res);
        //这里写方法完成后要做什么
        //res 是这个接口捕捉到的数据，可以通过console.log（res）查看后用比如 res.flag  or  res.data....来获取
        let key = document.getElementById("keyImg"); //获取id=keyImg 的Dom元素
        key.src = res.data.data.img; //给 id=keyImg 的Dom元素 的src 赋值
        let uuid = res.data.data.uuid;
        localStorage.setItem("uuid", uuid); //存本地

        if (localStorage.getItem("token")&&localStorage.getItem("check")) {
          this.$http({
            method:"post",
            url:"/user/loginByToken"
          }).then(res=>{
             if (res.data.data.role == "0") {
            //老黄给的用来判断用户类型的字段(role)
            this.$router.push("/home");
          } else if (res.data.data.role == "1") {
            this.$router.push("/teacherHome");
          } else if (res.data.data.role == "2") {
            this.$router.push("/rooterHome");
          }
          this.$message.success({
            message:"已为您自动登录",
            duration:"1000"
          })
          })
        }
      });
      console.log("验证码已刷新");
      // this.$message.success({
      //   message:"验证码已刷新",
      //   duration:500
      //   })
    },
    async tohome() {
      //登录方法
      localStorage.setItem("check",this.checked)
      let uuid = localStorage.getItem("uuid"); //从本地取
      let data = {
        // post 模板
        code: this.loginform.checkKey,
        password: this.loginform.password,
        username: this.loginform.username,
      };
      this.$http({
        method: "post",
        url: "/user/login",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded", //这句可以不用写，因为我再main.js里放了
          checkCode: uuid, //要不要放请求头要听后端的
        },
        data: QS.stringify(data), //QS要在顶部引用 ， import QS from 'qs'
      }).then((res) => {
        if (res.data.flag !== true) {
          this.checkKey();
          return this.$message.error(res.data.msg);
        } else {
          let token = res.data.data.token;
          localStorage.setItem("token", token);
          this.$message.success({
            message: "登录成功",
            duration: 700,
          });
          if (res.data.data.role == "0") {
            //老黄给的用来判断用户类型的字段(role)
            this.$router.push("/home");
          } else if (res.data.data.role == "1") {
            this.$router.push("/teacherHome");
          } else if (res.data.data.role == "2") {
            this.$router.push("/rooterHome");
          }
          window.sessionStorage.setItem("Id", res.data.data.id); //存进页面存在期间就一直存在的session中
          window.sessionStorage.setItem("role", res.data.data.role); //页面关闭、毁灭后session清空
        }
      });
    },
  },
};
</script>

<style>
* {
  margin: 0;
}
body {
  background-image: url("../assets/loginBG.jpg");
}
.loginBox {
  width: 100%;
  height: 100%;
}

.loginMainBox {
  width: 60%;
  height: 568px;
  margin-top: 5%;
  margin-left: 20%;
  /* border: 1px solid red; */
  background: url("../assets/login.png") no-repeat;
  background-size: 100%;
}
.loginbrand {
  display: flex;
  flex-direction: column;
  position: relative;
  left: 48%;
  /* border: solid 1px red; */
  height: 100%;
  width: 52%;
}
.loginbrand .passward {
  display: flex;
  flex-direction: row;
}
.usernameform {
  position: relative;
  top: 30%;
  left: 13%;
  width: 350px;
  /* border: 1px solid red; */
}
.keyimgbox {
  display: flex;
  flex-direction: row;
}
.keyimg {
  width: 50%;
  /* float:left; */
}
.keyimg img {
  width: 100%;
}
.remeberMe {
  padding-left: 30%;
  position: relative;
  top: -10%;
}
.login_register {
  padding-left: 25%;
}
</style>


