import Vue from 'vue'
import Router from 'vue-router'

//学生
import Home from '@/components/student/layout/Home.vue'
import SClass from '@/components/student/SClass.vue'
import MyClass from '@/components/student/MyClass.vue'
import MyScore from '@/components/student/MyScore.vue'
//首页
import Firstpage from '@/components/FP/Firstpage.vue'
import TFP from '@/components/FP/TFP.vue'
import RFP from '@/components/FP/RFP.vue'
//教师
import TeacherHome from '@/components/teacher/layout/TeacherHome.vue'
import Toclass from '@/components/teacher/Toclass.vue'
import Createclass from '@/components/teacher/Createclass.vue'
import Mystudent from '@/components/teacher/Mystudent.vue'
//管理员
import RooterHome from '@/components/rooter/layout/RooterHome.vue'
import ManageClass from '@/components/rooter/ManageClass.vue'
import ManageUser from '@/components/rooter/ManageUser.vue'
import CheckClass from '@/components/rooter/CheckClass.vue'

import Login from '@/components/Login.vue'
import axios from 'axios'
// import Vuex from 'vuex'
// Vue.use(Vuex)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/login',
      name: 'login',
      component: Login,
    },
    { 
      path: '/',
      redirect:'/login',
    },
    {
      path:'/home',
      component: Home,
      redirect:'/firstpage',
      children: [
        // { path: '/home',redirect:'/home/firstpage',component: Firstpage },
        { path: '/firstpage',component: Firstpage },
        { path: '/sclass',component: SClass },
        { path: '/myclass',component: MyClass },
        { path: '/myscore',component: MyScore },
      ]
    },
    {
      path:'/teacherHome',
      component: TeacherHome,
      redirect:'/tFP',
      children: [
         { path: '/tFP',component: TFP },
         { path: '/toclass',component: Toclass },
         { path: '/createclass',component:Createclass},
         { path: '/mystudent',component:Mystudent },
      ]
    },
    {
      path:'/rooterHome',
      component: RooterHome,
      redirect:'/rFP',
      children: [
         { path: '/rFP',component: RFP },
        { path: '/manageClass',component:  ManageClass },
        { path: '/manageUser',component: ManageUser  },
        { path: '/checkClass',component: CheckClass  },
      ]
    },
  ],
      
})
