<template>
  <div class="tableBox">
      <el-table
        :data="allClassForm"
        border
        style="
          width: 80%;
          margin: auto;
          border-right: 2px solid #737373;
          border-bottom: 2px solid #737373;
        "
      >
        <el-table-column prop="number" label="序号" width="180">
        </el-table-column>
        <el-table-column prop="class_name" label="课程名称" width="480" align="left">
        </el-table-column>
        <el-table-column prop="places" label="名额" width="180" align="center">
        </el-table-column>
        <el-table-column prop="class_category" label="课程类型" width="480" align="center">
        </el-table-column>
        <el-table-column prop="operate" label="操作">
          <template slot-scope="scope">
            <el-button type="text" @click="abandon_class(scope.row)"
              >移除课程</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        small
        @current-change="handleCurrentChange"
        :current-page="currentForm.pageNum"
        layout="prev, pager, next"
        :total="currentForm.total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="currentForm.pageSize"
        style="margin-left: 10%; margin-top: 20px"
      >
      </el-pagination>
    </div>
</template>

<script>
import QS from 'qs';
export default {
data(){
    return{
        allClassForm:[],
        currentForm:{
            pageNum:1,
            pageSize:5,
            total:0,
        },
    }
},
created(){
this.initial();
},
methods:{
    
    initial(){
        this.getAccessClass()
    },
    getAccessClass(){
        this.allClassForm=[]
        const params = new URLSearchParams
        params.append("pageNum",this.currentForm.pageNum)
        params.append("pageSize",this.currentForm.pageSize)
        params.append("className","")
        params.append("classSign","")
        params.append("classTerm","")
        params.append("classType","")
        this.$http({
            method:'get',
            url:"/admin/checkClassByCondition",
            params:params
        }).then(res=>{
            console.log("checkClassByCondition");
            console.log(res);
            let state = res.data.data.records
            console.log("state");
            console.log(state);
            let i;
            for(i in state){
                if (state[i].classSign==1) {//判断是否通过审核
                     this.allClassForm.push({
                    class_id:state[i].id,
                    class_name:state[i].className,
                    class_grade:state[i].classTerm,
                    class_category:state[i].classType,
                    number:parseInt(i)+1+(this.currentForm.pageNum-1)*5,
                    places:state[i].classFree,
                })
                }
            }
            this.currentForm.total=res.data.data.total
            console.log(this.allClassForm);
        })
    },
    abandon_class(row){
        let data={
              "classId":this.allClassForm[row.number-1].class_id,
            "className":this.allClassForm[row.number-1].class_name,
            "classSign":2,
            "classTerm":this.allClassForm[row.number-1].class_grade,
            "classType":this.allClassForm[row.number-1].class_category,
        }
        console.log(data);
        this.$http({
            method:'post',
            url:'/admin/updateClass',
            data:QS.stringify(data)
        }).then(res=>{
            console.log(res);
            this.getAccessClass()
        })        
    },
        handleCurrentChange(newPage){
        this.currentForm.pageNum=newPage
        this.getAccessClass()
    }
}
}
</script>

<style>
.tableBox{
    position: relative;
    top: 20%;
}
.tableBox_inner {
  border: 2px solid #737373;
  margin: 20px;
  padding: 0;
  height: 80%;
}
.el-table td,
.el-table th {
  border: 2px solid #737373;
}
</style>


