<template>
  <div class="mainContainer1" id="mainContainer1"></div>
</template>

<script>
import * as echarts from "echarts";
export default {
  created(){
    // this.initial()
  },
  mounted() {
    this.echart01();
  },

  methods: {
    // initial(){
    //   let role = JSON.parse(sessionStorage.getItem("role"))
    //   if(role!='0'){
    //     this.$router.push('/login')
    //   }
    // },
    echart01() {
      var chartDom = document.getElementById("mainContainer1");
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        graphic: {
          elements: [
            {
              type: "text",
              left: 410,
              top: 200,
              style: {
                text: "魔方教育教务系统",
                fontSize: 80,
                fontWeight: "bold",
                lineDash: [0, 200],
                lineDashOffset: 0,
                fill: "transparent",
                stroke: "#fff",
                lineWidth: 1,
              },
              keyframeAnimation: {
                duration: 3000,
                loop: false,
                keyframes: [
                  {
                    percent: 0.7,
                    style: {
                      fill: "transparent",
                      lineDashOffset: 200,
                      lineDash: [200, 0],
                    },
                  },
                  {
                    // Stop for a while.
                    percent: 0.8,
                    style: {
                      fill: "transparent",
                    },
                  },
                  {
                    percent: 1,
                    style: {
                      fill: "white",
                    },
                  },
                ],
              },
            },
          ],
        },
      };

      option && myChart.setOption(option);
    },
  },
};
</script>
<style>
.homeBox {
  width: 100%;
  height: 800px;
}
.mainContainer1 {
  position: relative;
  top: -3.5%;
  left: -4%;
  width: 105.2%;
  height: 106%;
  background-image: url("../../assets/frontpageBG.png");
}
</style>


