<template>
  <div class="navtopBox">
      <el-button type='text' @click="toFirstPage()" class="title"><h1 ><span>魔方教育</span>教务系统</h1></el-button>
      <el-menu 
      class="el-menu-demo" 
      mode="horizontal" 
      @open="handleOpen"
      @close="handleClose"
      @select="handleclick"
      >
        <el-menu-item index="sclass">去选课</el-menu-item>
        <el-menu-item index="myclass">我的课程</el-menu-item>
        <el-menu-item index="myscore">我的成绩</el-menu-item>
        </el-menu>
        <div class="backlogin">
        <el-col  >
            <el-dropdown>
            <span class='userBox'>
               <p> <span class="circle"> <i class="el-icon-user "></i></span><span  :v-model="role">{{role}}</span> </p>
            </span>
            <el-dropdown-menu slot="dropdown">
                <div  @click="backlogin()"><el-dropdown-item class="backItem">退出</el-dropdown-item></div>
                 <!-- <el-divider style="width:20px; margin:0!important;"></el-divider> -->
                <div  @click="backlogin()"><el-dropdown-item class="backItem">注销</el-dropdown-item></div>
            </el-dropdown-menu>
            </el-dropdown>
        </el-col>
        </div>
        <!-- <el-button type='text' class="backlogin" @click="backlogin()">退出</el-button> -->
  </div>
</template>

<script>
export default {
    created(){
        this.getRole();
    },
    data(){
        return{
            
            role:''
        }
    },
methods:{
    getRole(){
        let role=JSON.parse(sessionStorage.getItem("role")); 
        if(role=='0'){
            this.role='学生'
        }else if(role=='1'){
            this.role='教师'
        }else{
            this.role='管理员'
        }
    },
    toFirstPage(){
        this.$router.push('/firstpage')
    },
    handleclick(key,keypath){
        console.log(key,keypath);
        this.$router.push({path:key})
    },
    handleOpen(key,keypath){
        console.log(key,keypath)
    },
    handleClose(key,keypath){
        console.log(key,keypath)
    },
    backlogin(){
                localStorage.clear()
        window.sessionStorage.clear()
        this.$router.push('/login')
    }
}
}
</script>

<style>
.navtopBox{
    display: flex;
    flex-direction: row;
}
.title{
    margin-left: 20%;
}
.title h1{
    color:#474545;
}
.title span{
    color: #e95332;
}
.el-menu {
    background-color: rgba(0,0,0,0)!important;
    margin-left: 20%;
}
.el-menu-item{
    color: #242323!important;
    font-weight: 600;
    font-size: 18px;
}
.el-menu-item.isactive{
    color: #72db9d!important;
    border-bottom: #72db9d;
}
.backlogin{
color: #a1e7be;
position: absolute;
top: 1%;
right: 20px;
font-weight: 900;
font-size: 20px;
}
.userBox{
    color: #a1e7be;
    display: flex;
    flex-direction: row;
    /* padding: 20px; */
    font-size: 20px;
    position: relative;
    top: 10px;
}
.userBox i{
    font-weight: 1000;
    margin-left: 5px;
    margin-right: 5px;
}
.circle{
    border:2px solid #a1e7be;
    border-radius:50%;
    
    margin-right: 5px;
    
}
.backItem{
     color: #a1e7be;
}
</style>


