<template>
  <div class="tableBox">
      <el-table
        ref="multipleTable"
         tooltip-effect="dark"
        :data="checkClassForm"
        border
        @selection-change="handleSelectionChange"
        style="
          width: 80%;
          margin: auto;
          border-right: 2px solid #737373;
          border-bottom: 2px solid #737373;
        "
      >
      <el-table-column
      type="selection"
      width="55">
    </el-table-column>
        <el-table-column prop="number" label="序号" width="60" align="center">
        </el-table-column>
        <el-table-column prop="class_name" label="课程名称" width="380" align="center">
        </el-table-column>
        <el-table-column prop="places" label="名额" width="130" align="center">
        </el-table-column>
        <el-table-column prop="class_category" label="课程类型" width="280" align="center">
        </el-table-column>
        <el-table-column prop="class_grade" label="年级" width="80" align="center">
        </el-table-column>
        <el-table-column prop="class_status" label="状态" width="80" align="center">
        </el-table-column>
        <el-table-column prop="operate" label="操作" width="368" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="abandon_class(scope.row)"
              >审核不通过</el-button
            >
            <el-button type="warn" @click="access_class(scope.row)"
              >审核通过</el-button
            >
          </template>
        </el-table-column>
      </el-table>
              <!-- @size-change="handleSizeChange" -->
      <el-pagination
        small

        @current-change="handleCurrentChange"
        :current-page="currentForm.pageNum"
        layout="prev, pager, next"
        :total="currentForm.total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="currentForm.pageSize"
        style="margin-left: 10%; margin-top: 20px"
      >
      </el-pagination>
    </div>
</template>

<script>
import QS from "qs"
export default {
data(){
    return{
        checkClassForm:[],
        currentForm:{
            pageNum:1,
            pageSize:5,
            total:0
        },
        multipleSelection:[],
    }
},
created(){
this.initial();
},
methods:{
    initial(){
        let role = window.sessionStorage.getItem("role")
        console.log("role="+role);
        if(role!=2){
            this.$router.push("/login")
        }
        this.getAllClass()
    },
    getAllClass(){
        this.checkClassForm=[]
        const params = new URLSearchParams
        params.append("pageNum",this.currentForm.pageNum)
        params.append("pageSize",this.currentForm.pageSize)
        params.append("className","")
        params.append("classSign","")
        params.append("classTerm","")
        params.append("classType","")
        this.$http({
            method:'get',
            url:"/admin/checkClassByCondition",
            params:params
        }).then(res=>{
            console.log("checkClassByCondition");
            console.log(res);
            let state = res.data.data.records
            console.log(state);
            let i;
            for(i in state){
                this.checkClassForm.push({
                    class_id:state[i].id,
                    class_name:state[i].className,
                    class_grade:state[i].classTerm,
                    class_category:state[i].classType,
                    number:parseInt(i)+1+(this.currentForm.pageNum-1)*5,
                    places:state[i].classFree,
                    class_status:state[i].classSign==2?"未通过":state[i].classSign==1?"已通过":"待审核",
                })
            }
            this.currentForm.total=res.data.data.total
            console.log(this.checkClassForm);
        })
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
      },
    abandon_class(row){
        let data={
              "classId":this.checkClassForm[row.number-1].class_id,
            "className":this.checkClassForm[row.number-1].class_name,
            "classSign":2,
            "classTerm":this.checkClassForm[row.number-1].class_grade,
            "classType":this.checkClassForm[row.number-1].class_category,
        }
        console.log(data);
        this.$http({
            method:'post',
            url:'/admin/updateClass',
            data:QS.stringify(data)
        }).then(res=>{
            console.log(res);
            this.getAllClass()
        })
        //  this.checkClassForm[row.number-1].status="未通过"
        //  console.log(this.checkClassForm[row.number-1].status);
        
    },
    access_class(row){
        let data={
              "classId":this.checkClassForm[row.number-1].class_id,
            "className":this.checkClassForm[row.number-1].class_name,
            "classSign":1,
            "classTerm":this.checkClassForm[row.number-1].class_grade,
            "classType":this.checkClassForm[row.number-1].class_category,
        }
        console.log(data);
        this.$http({
            method:'post',
            url:'/admin/updateClass',
            data:QS.stringify(data)
        }).then(res=>{
            console.log(res);
            this.getAllClass()
        })
        // this.checkClassForm[row.number-1].status="已通过"
        // console.log(this.checkClassForm[row.number-1].status);
        
    },
    handleCurrentChange(newPage){
        this.currentForm.pageNum=newPage
        this.getAllClass()
    }
}
}
</script>

<style>
.tableBox_inner {
  border: 2px solid #737373;
  margin: 20px;
  padding: 0;
  height: 80%;
}
.el-table td,
.el-table th {
  border: 2px solid #737373;
}
</style>


