<template>
  <div class="tableBox">
    <el-button @click="comeback()" class="back"
      ><i class="el-icon-s-home"></i><i class="el-icon-back"></i
    ></el-button>
    <el-table
      :data="studentForm"
      border
      style="
        width: 80%;
        margin: auto;
        border-right: 2px solid #737373;
        border-bottom: 2px solid #737373;
      "
    >
      <el-table-column prop="number" label="序号" width="180">
      </el-table-column>
      <el-table-column prop="stuName" label="学生姓名" width="180">
      </el-table-column>
      <el-table-column prop="stuMajor" label="专业" width="380">
      </el-table-column>
      <el-table-column prop="stuGrade" label="年级" width="180">
      </el-table-column>
      <el-table-column prop="stuScore" label="成绩" width="180">
      </el-table-column>
      <el-table-column prop="operate" label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="openDialog(scope.row)"
            >录入成绩</el-button
          >
          <el-dialog :visible.sync="dialogVisible" width="30%">
            <el-form label-width="80px" :model="powerForm">
              <el-form-item label="考试占比">
                <el-input v-model="powerForm.classTestScore"></el-input>
              </el-form-item>
              <el-form-item label="平时分">
                <el-input v-model="powerForm.usualScore"></el-input>
              </el-form-item>
              <el-form-item label="考试成绩">
                <el-input v-model="powerForm.testScore"></el-input>
              </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
              <el-button @click="this.dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="givePower(list)"
                >确 定</el-button
              >
            </span>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        small
        @current-change="handleCurrentChange"
        :current-page="currentForm.pageNum"
        layout="prev, pager, next"
        :total="currentForm.total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="currentForm.pageSize"
        style="margin-left: 10%; margin-top: 20px"
      >
      </el-pagination>
  </div>
</template>

<script>
import QS from "qs";
export default {
  data() {
    return {
      class_id:0,
      dialogVisible: false,
      list: [],
      studentForm: [],
      currentForm: {
        total: 0,
        pageNum: 1,
        pageSize: 5,
      },
      powerForm: {
        classTestScore: "",
        usualScore: "",
        testScore: "",
      },
    };
  },
  created() {
    this.initial();
  },
  methods: {
    initial() {
      let className = window.sessionStorage.getItem("class_name"); //依据className找到class_id
      let teaId = parseInt(window.sessionStorage.getItem("Id"));
      const params = new URLSearchParams();
      params.append("teaId", teaId);
      params.append("pageNum", 1);
      params.append("pageSize", 100);
      this.$http({
        method: "get",
        url: "teacher/teaCheckMyClass", //老师查看自己的课
        params: params,
      }).then((res) => {
        let i;
        var class_id;
        let state = res.data.data.records;
        console.log(state);
        for (i in state) {
          if (className == state[i].className) {
            class_id = state[i].id; //找到class_id并保存
          }
        }
        this.class_id=class_id
      this.studentForm=[]
        const query = new URLSearchParams();
        query.append("classId", class_id);
        query.append("pageNum", this.currentForm.pageNum);
        query.append("pageSize", 100);
        this.$http({
          method: "get",
          url: "teacher/teaCheckStudent", //查找这门课有哪些学生选了
          params: query,
        }).then((res) => {
          console.log(res);
          let state = res.data.data;
          this.currentForm.total=res.data.data.length
          console.log(this.currentForm.total);
        });

        this.getMyStudent()
      });
    },
    getMyStudent(){
      let class_id=this.class_id
      this.studentForm=[]
        const query = new URLSearchParams();
        query.append("classId", class_id);
        query.append("pageNum", this.currentForm.pageNum);
        query.append("pageSize", this.currentForm.pageSize);
        this.$http({
          method: "get",
          url: "teacher/teaCheckStudent", //查找这门课有哪些学生选了
          params: query,
        }).then((res) => {
          console.log(res);
          let state = res.data.data;
          let i;
          console.log("找找");
          console.log(state);
          if (state.length == 0) {
            this.$message.error({
              message: "课程暂未发布或没有学生选择",
              duration: "1000",
            });
          }
          for (i in state) {
            this.studentForm.push({
              number: parseInt(i) + 1+(this.currentForm.pageNum-1)*5,
              stuGrade: state[i].student.stuGrade,
              stuMajor: state[i].student.stuMajor,
              stuName: state[i].student.stuName,
              stuScore: state[i].classScore < 0 ? 0 : state[i].classScore,
              class_id: state[i].classId,
              stuId: state[i].stuId,
            });
          }
        });
    },
    openDialog(row) {
      this.list = row;
      console.log("list");
      console.log(this.list);
      this.dialogVisible = true;
    },
    givePower(row) {
      console.log("log");
      console.log(row);
      console.log(row.number);
      console.log(this.studentForm[row.number - 1].stuId);
      let data = {
        classId: this.studentForm[row.number - 1].class_id,
        stuIds: this.studentForm[row.number - 1].stuId,
        classTestScore: this.powerForm.classTestScore,
        usualScore: this.powerForm.usualScore,
        testScore: this.powerForm.testScore,
      };
      console.log("classTestScore=" + this.powerForm.classTestScore);
      this.$http({
        method: "post",
        url: "/teacher/teaRecordScore",
        data: QS.stringify(data),
      });

      row.stuScore =
        this.powerForm.testScore * this.powerForm.classTestScore * 0.01 +
        this.powerForm.usualScore * (1 - this.powerForm.classTestScore * 0.01);
      this.dialogVisible = false;
    },
    comeback() {
      window.history.back();
    },
    handleCurrentChange(newPage){
        this.currentForm.pageNum=newPage
        this.getMyStudent()
    }
  },
};
</script>

<style>
.back {
  border: 1px solid #f1d3de;
  width: 80px;
  text-align: center;
  border-radius: 5px;
  background-color: #ebeef5;
  margin-left: 30px;
  margin-top: 20px;
}
</style>


