      <div style="border:3px solid #a6a6a6; margin:1%; height:95%;">
          <router-view></router-view>
      </div>
    </div>     
  </div>
</template>

<script>

import Navtop from './RNavtop.vue'
export default {
    components:{
        Navtop
    }

}
</script>

<style>

.homeBox{
    width: 100%;
    height: 800px;
}
.mainContainer{
    position: absolute;
    top: 8%;
    width: 99%;
    height: 88%;
    background-color: #fff;
}
</style>


