<template>
  <div class="selectBox">
    <div class="tableBox">
      <div class="addbox">
        <el-button
          type="primary"
          icon="el-icon-edit"
          circle
          @click="dialogVisible = true"
        ></el-button>

        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
          <el-form
            label-width="80px"
            :model="newForm"
          >
            <el-form-item label="课程名称">
              <el-input v-model="newForm.class_name"></el-input>
            </el-form-item>
            <el-form-item label="课程年级">
              <el-input v-model="newForm.class_grade"></el-input>
            </el-form-item>
            <el-form-item label="课程类型" class="dialog">
              <div
                style="width: 40%; float: left; position: relative; left: -70px"
              >
                <el-select
                  v-model="newForm.class_category"
                  slot="prepend"
                  placeholder="请选择"
                  style="width: 180px; height: 25px; border-radius: 5px"
                >
                  <el-option
                    label="人文素养类·艺术"
                    value="人文素养类·艺术"
                  ></el-option>
                  <el-option label="人文素养类" value="人文素养类"></el-option>
                  <el-option label="职业素养类" value="职业素养类"></el-option>
                </el-select>
              </div>
              <div style="width: 60%; float: left">
                <el-form-item label="成绩占比">
                  <el-input v-model="newForm.class_perAll"></el-input>
                </el-form-item>
              </div>
            </el-form-item>

            <el-form-item label="学生名额">
              <el-input v-model="newForm.places"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addNewClass()"
              >确 定</el-button
            >
          </span>
        </el-dialog>
      </div>
      <el-table
        :data="teaClassForm"
        border
        style="
          width: 80%;
          margin: auto;
          border-right: 2px solid #737373;
          border-bottom: 2px solid #737373;
        "
      >
        <el-table-column prop="number" label="序号" width="160">
        </el-table-column>
        <el-table-column prop="places" label="名额" width="180">
        </el-table-column>
        <el-table-column prop="class_name" label="课程名称" width="480">
        </el-table-column>
        <el-table-column prop="class_category" label="课程类型" width="480">
        </el-table-column>
        <el-table-column prop="operate" label="操作" width="180">
          <template slot-scope="scope">
            <el-button type="text" @click="MyStudent(scope.row)"
              >我的学生</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        small
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentForm.pageNum"
        layout="prev, pager, next"
        :total="currentForm.total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="currentForm.pageSize"
        style="margin-left: 10%; margin-top: 20px"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import QS from "qs"
export default {
  data() {
    return {
      newForm: {
        class_name: "",
        class_perAll: "",
        class_grade: "",
        class_category: "",
        places: "",
      },
      dialogVisible: false,
      drawer: false,
      currentForm: {
        total: 0,
        pageNum: 1,
        pageSize: 5,
      },
      teaClassForm: [],
      teaClassForm_id: [],
    };
  },
  created() {
    this.initial();
  },
  methods: {
    initial() {
      let role = JSON.parse(sessionStorage.getItem("role"));
      if (role != "1") {
        this.$router.push("/login");
        this.$message.error("请先登录");
      }
      const params = new URLSearchParams();
      let teaId = parseInt(window.sessionStorage.getItem("Id"));
      console.log(localStorage.getItem("token"));
      params.append("teaId", teaId);
      params.append("pageNum", this.currentForm.pageNum);
      params.append("pageSize", this.currentForm.pageSize);
      this.$http({
        method: "get",
        url: "/teacher/teaCheckMyClass",
        params: params,
      }).then((res) => {
        console.log("res");
        console.log(res.data.data);
        let state = res.data.data.records;
        let i;
        for (i in state) {
          this.teaClassForm.push({
            number: parseInt(i) + 1,
            class_name: state[i].className,
            class_category: state[i].classType,
            places: state[i].classFree,
          });
        }
        console.log(this.teaClassForm);

        this.currentForm.total = res.data.data.total;
      });
    },
    MyStudent(row) {
      window.sessionStorage.setItem("class_name", row.class_name);
      this.$router.push("/mystudent");
    },
    getClass() {
      console.log("get");
      const params = new URLSearchParams();
      let teaId = parseInt(window.sessionStorage.getItem("Id"));
      params.append("teaId", teaId);
      params.append("pageNum", this.currentForm.pageNum);
      params.append("pageSize", this.currentForm.pageSize);
      this.$http({
        method: "get",
        url: "/teacher/teaCheckMyClass",
        params: params,
      }).then((res) => {
        console.log("res");
        console.log(res.data.data);
        let state = res.data.data.records;
        let i;
        console.log(state);
        for (i in state) {
          console.log(i);
          this.teaClassForm.push({
            number:
              parseInt(i) +
              (this.currentForm.pageNum - 1) * this.currentForm.pageSize +
              1,
            class_name: state[i].className,
            class_category: state[i].classType,
            places: state[i].classFree,
          });
        }
      });
    },
    addNewClass(){
        console.log(this.newForm);
        let teaId = window.sessionStorage.getItem("Id")
let data={
"className":this.newForm.class_name,
"classTerm":this.newForm.class_grade,
"classType":this.newForm.class_category,
"teaId":teaId,
"classTestScore":this.newForm.class_perAll,
"classFree":this.newForm.places,
}
        this.$http({
          method:"post",
          url:"/teacher/teaSetClass",
          data:QS.stringify(data)
        })
    },

    handleSizeChange(newSize) {
      this.currentForm.pageSize = newSize;
      this.getClass();
    },
    handleCurrentChange(newPage) {
      this.teaClassForm = [];
      this.currentForm.pageNum = newPage;
      this.getClass();
    },
    recommand() {},

    handleInputConfirm() {},
    transfer() {
      //接口【接收随机符合条件的课程5个，替换intelligent_classForm数组】
    },
    clearSearchClass() {
      this.searchForm = [];
    },
  },
};
</script>

<style>
.dialog {
  display: flex;
  flex-direction: row !important;
  /* margin: 0; */
  padding: 0;
}
.top_sreach {
  width: 80%;
  height: 60px;
  background-color: white;
  border-radius: 10px;
  margin-left: 10%;
}
.top_item {
  line-height: 60px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  margin-left: 20px;
  float: left;
  color: #6b6b6b;
}
.search_input {
  line-height: 60px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  float: left;
  margin-left: 10px;
}
.input_box {
  width: 180px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #b6b6b6;
}
.addbox {
  position: absolute;
  right: 5%;
}
.tableBox {
  position: relative;
  top: 50px;
  display: flex;
  flex-direction: column;
}
.tableBox_inner {
  border: 2px solid #737373;
  margin: 20px;
  padding: 0;
  height: 80%;
}
.el-table td,
.el-table th {
  border: 2px solid #737373;
}
.mySclass {
  border: 2px solid #737373;
  width: 80%;
  height: 80px;
  margin-top: 1%;
  margin-left: 10%;
  display: flex;
  flex-direction: column;
}
.classTitle {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50%;
  position: relative;
  top: 10%;
  margin-bottom: 5px;
}
.classTitle h1 {
  font-size: 25px;
  font-weight: 400;
  margin-left: 20px;
}
.mySclassBox {
  margin-left: 35px;
  font-size: 12px;
  font-weight: 400;
}
.mySclassBox .el-tag {
  margin-right: 25px;
}
</style>


