<template>
  <div class="selectBox">
    <div class="top_sreach">
      <div class="top_item">课程名称:</div>
      <div class="search_input">
        <input type="text" class="input_box" v-model="searchForm.name" />
      </div>
      <div class="top_item">课程类型:</div>
      <div class="search_input">
        <el-select
          v-model="searchForm.select_kind"
          slot="prepend"
          placeholder="请选择"
          style="width: 180px; height: 25px; border-radius: 5px"
        >
          <el-option
            label="人文素养类·艺术"
            value="人文素养类·艺术"
          ></el-option>
          <el-option label="人文素养类" value="人文素养类"></el-option>
          <el-option label="职业素养类" value="职业素养类"></el-option>
        </el-select>
      </div>

      <div class="top_item">学生年级:</div>
      <div class="search_input">
        <el-select
          v-model="searchForm.select_grade"
          slot="prepend"
          placeholder="请选择"
          style="width: 180px; height: 25px; border-radius: 5px"
        >
          <el-option label="大一" value="大一"></el-option>
          <el-option label="大二" value="大二"></el-option>
          <el-option label="大三" value="大三"></el-option>
        </el-select>
      </div>
      <div class="search_input">
        <el-button type="success" round @click="getClass()"
          ><i class="el-icon-s-promotion"></i>搜索</el-button
        >
      </div>
      <div class="search_input">
        <el-button type="info" round @click="clearSearchClass()"
          ><i class="el-icon-delete-solid"></i>清空</el-button
        >
      </div>
    </div>
    <div class="tableBox">
      <el-table
        :data="classForm"
        border
        style="
          width: 80%;
          margin: auto;
          border-right: 2px solid #737373;
          border-bottom: 2px solid #737373;
        "
      >
        <el-table-column prop="number" label="序号" width="180">
        </el-table-column>
        <el-table-column prop="class_name" label="课程名称" width="180">
        </el-table-column>
        <el-table-column prop="places" label="名额" width="180">
        </el-table-column>
        <el-table-column prop="class_category" label="课程类型" width="180">
        </el-table-column>
        <el-table-column prop="operate" label="操作">
          <template slot-scope="scope">
            <el-button type="text" @click="add_class(scope.row)"
              >加入课程</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        small
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentForm.pageNum"
        layout="prev, pager, next"
        :total="currentForm.total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="currentForm.pageSize"
        style="margin-left: 10%; margin-top: 20px"
      >
      </el-pagination>
    </div>
    <div class="mySclass">
      <div class="classTitle">
        <h1>我的选课:</h1>
        <el-button
          type="warning"
          icon="el-icon-star-off"
          plain
          round
          @click="drawer = true"
          style="position: absolute; right: 12%"
          >智能推荐</el-button
        >
        <el-drawer title="智能推荐" :visible.sync="drawer" size="45%">
          <div class="tableBox_inner">
            <div class="top_sreach">
              <div class="top_item">课程名称:</div>
              <div class="search_input">
                <input type="text" class="input_box" v-model="classForm.name" />
              </div>
              <div class="search_input">
                <el-button type="success" round
                  ><i class="el-icon-s-promotion"></i>搜索</el-button
                >
                <div style="position: absolute; right: 5%; top: 13%">
                  <el-button
                    type="text"
                    small
                    style="color: #e95332"
                    @click="transfer()"
                    >换一换</el-button
                  >
                </div>
              </div>
            </div>
            <el-table
              :data="intelligent_classForm"
              border
              style="
                width: 80%;
                margin: auto;
                border-right: 2px solid #737373;
                border-bottom: 2px solid #737373;
              "
            >
              <el-table-column prop="number" label="序号" width="60">
              </el-table-column>
              <el-table-column prop="class_name" label="课程名称" width="140">
              </el-table-column>
              <el-table-column prop="places" label="名额" width="100">
              </el-table-column>
              <el-table-column
                prop="class_category"
                label="课程类型"
                width="130"
              >
              </el-table-column>
              <el-table-column prop="operate" label="操作">
                <template slot-scope="scope">
                  　　<el-button type="text" @click="add_Intclass(scope.row)"
                    >加入课程</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-drawer>
      </div>
      <div class="mySclassBox">
        <el-tag
          v-for="item in class_tags"
          :key="item.mark"
          effect="plain"
          closable
          @close="deleteclass(item)"
        >
          {{ item.class_name }}
        </el-tag>
      </div>
    </div>
  </div>
</template>

<script>
import QS from "qs";
export default {
  data() {
    return {
      drawer: false,
      class_tags: [],
      currentForm: [
        {
          total: 5,
          pageNum: 1,
          pageSize: 5,
        },
      ],
      searchForm: [
        {
          name: "",
          select_kind: "",
          select_grade: "",
        },
      ],
      classForm: [],
      classForm_id: [],
      intelligent_classForm: [],
    };
  },
  created() {
    this.getbackItem();
  },
  methods: {
    async getbackItem() {
      let role = JSON.parse(sessionStorage.getItem("role"));
      if (role != "0") {
        this.$router.push("/login");
        this.$message.error("请先登录");
      }
      let stuId = JSON.parse(sessionStorage.getItem("Id"));
      var studentId = parseInt(stuId);
      let token = localStorage.getItem("token");
      //学生选了哪些课
      const params = new URLSearchParams();
      params.append("pageNum", "1");
      params.append("pageSize", "100");
      params.append("stuId", studentId);
      params.append("queryName", "");
      params.append("queryTerm", "");
      params.append("queryType", "");
      this.$http({
        method: "get",
        url: "/student/checkSelectedClass",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          token: token,
        },
        params: params,
      }).then((res) => {
        //有哪些课可以选
        const query = new URLSearchParams();
        query.append("pageNum", "1");
        query.append("pageSize", "100");
        query.append("stuId", "");
        query.append("queryName", "");
        query.append("queryTerm", "");
        query.append("queryType", "");
        this.$http({
          url: "/student/checkAllClass",
          method: "get",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            token: token,
          },
          params: query,
        }).then((res_1) => {
          let i;
          let k;
          let count = 0;
          let flag = "0";
          let state = res.data.data;
          let allstate = res_1.data.data.records;
          if (state.length == 0) {
            state = [1];
          }
          for (i in state) {
            count = 0;
            for (k in allstate) {
              count += 1;
              if (flag == "0") {
                //将allstate里全部item加入classFrom_id中，=0取消重复push
                this.classForm_id.push({
                  number: count,
                  class_id: allstate[k].id,
                });
              }
              if (state[i].id == allstate[k].id) {
                this.class_tags.push({
                  class_name: allstate[k].className,
                  number: count,
                });
              }
            }
            flag = "1";
          }
        });
      });

      //一共有哪些课
      const currentquery = new URLSearchParams();
      currentquery.append("pageNum", "1");
      currentquery.append("pageSize", "5");
      currentquery.append("stuId", "");
      currentquery.append("queryName", "");
      currentquery.append("queryTerm", "");
      currentquery.append("queryType", "");
      this.$http({
        method: "get",
        url: "student/checkAllClass",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          token: token,
        },
        params: currentquery,
      }).then((res) => {
        let count = 0;
        var j;
        let state = res.data.data.records;
        for (j in state) {
          count += 1;
          this.classForm.push({
            number: count,
            class_name: state[j].className,
            places: state[j].classFree,
            class_category: state[j].classType,
            operate: "加入课程",
          });
        }
        (this.currentForm.total = res.data.data.total),
          (this.currentForm.pageNum = res.data.data.current),
          (this.currentForm.pageSize = res.data.data.size);
      });

      console.log("-------");
      console.log(this.class_tags);
      console.log(this.classForm);
    },

    async add_class(row) {
      if(this.class_tags.length>=5){
          return this.$message.error({
            message:"超过可以选择的选修课数量！",
            duration:500
            });
        }
      let tag = { class_name: row.class_name, number: row.number };
      let stuId = JSON.parse(sessionStorage.getItem("Id"));
      var studentId = parseInt(stuId);
      let class_id = this.classForm_id[row.number - 1].class_id;
      
      let data = { classId: class_id, stuId: studentId };
      this.$http({
        method: "post",
        url: "/student/selectClass",
        data: QS.stringify(data),
      }).then((res) => {
        if (res.data.flag != true) {
          return this.$message.error({
            message:res.data.msg,
            duration:500
            });
        }
        row.places-=1
        this.class_tags.push(tag);
      });

      //接口【限制数量，限制重复，发送数组（class_tags），】
    },
    deleteclass(item) {
      console.log("classForm_id+item.number-1");
      console.log(this.classForm_id);
      console.log(item.number - 1);
      let class_id = this.classForm_id[item.number - 1].class_id;
      let stuId = JSON.parse(sessionStorage.getItem("Id"));
      var studentId = parseInt(stuId);
      this.$http.post(
        "/student/cancelClass?classId=" + class_id + "&stuId=" + studentId
      );
      this.class_tags.splice(this.class_tags.indexOf(item), 1);
    },
    async getClass() {
      //接口【查找课程】
      let a = this.searchForm.name;
      let b = this.searchForm.select_kind;
      let c = this.searchForm.select_grade;
      if (!a) {
        a = "";
      }
      if (!b) {
        b = "";
      }
      if (!c) {
        c = "";
      }
      const { data: res } = await this.$http.get(
        "student/checkAllClass?pageNum=" +
          this.currentForm.pageNum +
          "&pageSize=" +
          this.currentForm.pageSize +
          "&queryName=" +
          a +
          "&queryTerm=" +
          c +
          "&queryType=" +
          b
      );
      this.currentForm.total = res.data.total;
      var j;
      let state = res.data.records;
      let count = (this.currentForm.pageNum - 1) * this.currentForm.pageSize;
      this.classForm = [];
      for (j in state) {
        count += 1;
        this.classForm.push({
          number: count,
          mark: count,
          class_name: state[j].className,
          places: state[j].classFree,
          class_category: state[j].classType,
          operate: "加入课程",
        });
        // this.classForm_id.push({ number: count, class_id: state[j].id });
      }
    },
    add_Intclass() {},

    handleSizeChange(newSize) {
      this.currentForm.pageSize = newSize;
      this.getClass();
    },
    handleCurrentChange(newPage) {
      this.currentForm.pageNum = newPage;
      this.getClass();
    },
    recommand() {},

    handleInputConfirm() {},
    transfer() {
      //接口【接收随机符合条件的课程5个，替换intelligent_classForm数组】
    },
    clearSearchClass() {
      this.searchForm = [];
    },
  },
};
</script>

<style>
.top_sreach {
  width: 80%;
  height: 60px;
  background-color: white;
  border-radius: 10px;
  margin-left: 10%;
}
.top_item {
  line-height: 60px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  margin-left: 20px;
  float: left;
  color: #6b6b6b;
}
.search_input {
  line-height: 60px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
  float: left;
  margin-left: 10px;
}
.input_box {
  width: 180px;
  height: 25px;
  border-radius: 5px;
  border: 1px solid #b6b6b6;
}
.tableBox_inner {
  border: 2px solid #737373;
  margin: 20px;
  padding: 0;
  height: 80%;
}
.el-table td,
.el-table th {
  border: 2px solid #737373;
}
.mySclass {
  border: 2px solid #737373;
  width: 80%;
  height: 80px;
  margin-top: 4%;
  margin-left: 10%;
  display: flex;
  flex-direction: column;
}
.classTitle {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 50%;
  position: relative;
  top: 10%;
  margin-bottom: 5px;
}
.classTitle h1 {
  font-size: 25px;
  font-weight: 400;
  margin-left: 20px;
}
.mySclassBox {
  margin-left: 35px;
  font-size: 12px;
  font-weight: 400;
}
.mySclassBox .el-tag {
  margin-right: 25px;
}
</style>


