<template>
  <div class="classTableBox">
    <div class="topBox"></div>

    <div class="classBox">
      <el-table :data="dateFrom" border style="width: 100%">
        <el-table-column prop="learn_time" label="时间" width="180">
          <template slot-scope="scope">
            <el-button type="text" @click="add_class(scope.row)"
              >加入课程</el-button
            >
          </template>
        </el-table-column>
        <el-table-column prop="Monday" label="周一" width="180">
          <!-- <template slot-scope="scope">
            <div v-html="scope.row.mon"></div>
        </template> -->
        </el-table-column>
        <el-table-column prop="Tuesday" label="周二" width="180">
        </el-table-column>
        <el-table-column prop="Wednesday" label="周三"> </el-table-column>
        <el-table-column prop="Thursday" label="周四" width="180">
        </el-table-column>
        <el-table-column prop="Friday" label="周五" width="180">
        </el-table-column>
        <el-table-column prop="Saturday" label="周六"> </el-table-column>
        <el-table-column prop="Sunday" label="周日" width="180">
        </el-table-column>

        <!-- this.dataFrom.class+"<br/>"+this.dataFrom.time+"<br/>"+this.dataFrom.teacherName+"<br/>"+this.dataFrom.classroom, -->
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dateFrom: [
        {
          learn_time: "8:40-10:10",
          Monday: "",
          Tuesday: "",
          Wednesday: "",
          Tuesday: "",
          Friday: "",
          Saturday: "",
          Sunday: "",
        },
        {
          learn_time: "10:40-12:10",
          Monday: "",
          Tuesday: "",
          Wednesday: "",
          Tuesday: "",
          Friday: "",
          Saturday: "",
          Sunday: "",
        },
        {
          learn_time: "13:10-14:40",
          Monday: "",
          Tuesday: "",
          Wednesday: "",
          Tuesday: "",
          Friday: "",
          Saturday: "",
          Sunday: "",
        },
        {
          learn_time: "15:00-16:30",
          Monday: "",
          Tuesday: "",
          Wednesday: "",
          Tuesday: "",
          Friday: "",
          Saturday: "",
          Sunday: "",
        },
        {},
        {},
      ],
      dataFrom: [
        {
          class: "托马斯回旋原理",
          time: "{1-18周}",
          teacherName: "马大帅",
          classroom: "索思企业工匠坊101",
        },
      ],
    };
  },
  created(){
    this.initial()
  },
  methods:{
    initial(){
      let role = JSON.parse(sessionStorage.getItem("role"))
      if(role!='1'){
        this.$router.push('/login')
        this.$message.error('请先登录')
      }
    }
  }
};
</script>

<style>
</style>


